@import url("https://fonts.googleapis.com/css?family=Bubblegum+Sans");
.card {
  background-color: #e0f0ff;
  border: 1px solid #81D4FA;
  border-radius: 5px;
  color: #000000;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Bubblegum Sans', cursive;
  font-size: 1.4em;
  height: 2.5em;
  margin: 0.5em;
  width: 2em;
  -webkit-transition: background-color 0.5s, color 0.5s, margin-top 0.5s, opacity 1s, font-size 0.5s;
  -o-transition: background-color 0.5s, color 0.5s, margin-top 0.5s, opacity 1s, font-size 0.5s;
  transition: background-color 0.5s, color 0.5s, margin-top 0.5s, opacity 1s, font-size 0.5s; }
  .card__label {
    display: inline-block;
    margin: auto; }
  .card.selected {
    font-size: 1.6em;
    margin-top: -10em;
    opacity: 0; }
  .card i.fas {
    margin: auto; }



